@import "@/assets/scss/main.scss";

.form-wrapper {
  margin-bottom: 32px;
  width: 100%;
  height: 54px;
  background-color: $form-bg;

  label {
    padding: 2px 10.55px 2px 10.55px;
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 0px;
    color: $scale-gray8;
  }

  input {
    padding: 2px 10.55px 2px 10.55px;
    width: 100%;
    border: 0px;
    background-color: $form-bg;
    font-size: 16px;
    line-height: 26px;
    color: $scale-gray10;
    border-bottom: 2px solid $form-border;

    &:hover,
    &:focus {
      border-bottom-color: $sider-blue;
    }

    &:-webkit-autofill {
      -webkit-box-shadow: 0 0 0px 100px $form-bg inset;
    }
  }

  .input__info {
    color: $error-red;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
  }

  .input__counter {
    font-size: 12px;
    color: $scale-gray8;
    margin: 0px 0px 0px auto;
    text-align: right;
    padding: 0px;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
  }
}

button {
  width: 100%;
  height: 46px;
  border-radius: 50px;
  background: $brand-orange;
  font-size: 20px;
  color: $scale-gray1;
  padding: 8px 24px;
  margin: 8px 0 22px 0;
}

.router_wrapper {
  a {
    color: $primary-blue;
    text-decoration: underline;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
  }
}

::placeholder {
  color: $scale-gray6;
  opacity: 1;
}
